<template>
  <div class="home row no-gutters">
    <div class="col-sm-3">
      <div
        v-if="cityName.length"
        class="toolbox"
      >
        <div class="sticky-top bg-white shadow-sm p-2">
          <div class="form-group d-flex">
            <label
              for="cityName"
              class="mr-2 col-form-label text-right"
            >縣市</label>
            <div class="flex-fill">
              <select
                id="cityName"
                class="form-control"
                v-model="select.city"
                @change="select.area = ''"
              >
                <option value="">-- Select One --</option>
                <option
                  :value="c.CityName"
                  v-for="c in cityName"
                  :key="c.CityName"
                >
                  {{ c.CityName }}
                </option>
              </select>
            </div>
          </div>
          <div class="form-group d-flex">
            <label
              for="area"
              class="mr-2 col-form-label text-right"
            >地區</label>
            <div class="flex-fill">
              <select
                id="area"
                class="form-control"
                v-if="select.city.length"
                v-model="select.area"
                @change="updateSelect"
              >
                <option value="">-- Select One --</option>
                <option
                  :value="a.AreaName"
                  v-for="a in cityName.find((city) => city.CityName === select.city).AreaList"
                  :key="a.AreaName"
                >
                  {{ a.AreaName }}
                </option>
              </select>
            </div>
          </div>
          <p class="mb-0 small text-muted text-right">請先選擇區域查看（綠色表示還有數量）</p>
        </div>

        <ul class="list-group">
          <template v-for="(item, key) in data">
            <a
              class="list-group-item text-left"
              :key="key"
              v-if="item.hsnNm === select.city
                && item.townNm === select.area"
              :class="{ 'highlight': item.total.parseInt}"
              @click="penTo(item)"
            >
              <h3>{{ item.storeNm }}</h3>
              <p class="mb-0">
                剩餘數量：{{ item.total }}
              </p>
              <p class="mb-0">
                地址：
                <!-- <a :href="'https://www.google.com.tw/maps/place/' + item.addr " target="_blank"> -->
                <a :href="`https://www.google.com/maps/dir/?api=1&origin=${nowLocation.coords.latitude},+${nowLocation.coords.longitude}&destination=${item.addr}&travelmode=walking`" target="_blank">
                  {{ item.zipCd }} {{ item.addr }}
                </a>
              </p>
              <p class="mb-0">
                距離：
                {{ item.distance }}
              </p>
              <p class="mb-0">
                <small>更新日期: {{ item.updateDate }}</small>
              </p>
            </a>
          </template>
        </ul>
      </div>
    </div>
    <div class="col-sm-9">
      <div id="map"></div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
// import $ from 'jquery';
import L from 'leaflet';
import cityName from './assets/cityName.json';

let osmMap = {};

const iconsConfig = {
  iconSize: [25, 41],
  iconAnchor: [12, 41],
  popupAnchor: [1, -34],
  shadowSize: [41, 41],
};
const icons = {
  green: new L.Icon({
    iconUrl: 'https://cdn.rawgit.com/pointhi/leaflet-color-markers/master/img/marker-icon-2x-green.png',
    ...iconsConfig,
  }),
  grey: new L.Icon({
    iconUrl: 'https://raw.githubusercontent.com/pointhi/leaflet-color-markers/master/img/marker-icon-grey.png',
    ...iconsConfig,
  }),
};

const osm = {
  addMapMarker(x, y, item, distance) {
    let traDistance = distance;
    let unit = '公尺';
    if (traDistance > 999) {
      traDistance = Math.round(distance / 1000);
      unit = '公里';
    }
    const total = item.total.parseInt;
    const icon = total ? icons.green : icons.grey;
    L.marker([y, x], {
      icon,
    }).addTo(osmMap).bindPopup(`<strong>${item.storeNm}(${item.storeCd})</strong> <br>
    <strong>剩餘: ${item.total ? `${item.total} 個` : '未取得資料'}</strong><br>
    <strong>地址:</strong> <a href="https://www.google.com.tw/maps/place/${item.addr}" target="_blank">${item.zipCd} ${item.addr}</a><br>
    <strong>距離:</strong> 約 ${traDistance} ${unit}<br>
    <strong>營業時間:</strong> ${item.busiTime}
    <strong>電話:</strong> ${item.tel}<br>`);
  },
  removeMapMarker() {
    osmMap.eachLayer((layer) => {
      if (layer instanceof L.Marker) {
        osmMap.removeLayer(layer);
      }
    });
  },
  penTo(x, y, item, distance) {
    let traDistance = distance;
    let unit = '公尺';
    if (traDistance > 999) {
      traDistance = Math.round(distance / 1000);
      unit = '公里';
    }
    const total = item.total.parseInt;
    const icon = total ? icons.green : icons.grey;
    osmMap.panTo(new L.LatLng(y, x));
    L.marker([y, x], {
      icon,
    }).addTo(osmMap).bindPopup(`<strong>${item.storeNm}(${item.storeCd})</strong> <br>
    <strong>剩餘: ${item.total ? `${item.total} 個` : '未取得資料'}</strong><br>
    <strong>地址:</strong> <a href="https://www.google.com.tw/maps/place/${item.addr}" target="_blank">${item.zipCd} ${item.addr}</a><br>
    <strong>距離:</strong> 約 ${traDistance} ${unit}<br>
    <strong>營業時間:</strong> ${item.busiTime}
    <strong>電話:</strong> ${item.tel}<br>`);
  },
};

export default {
  name: 'home',
  data: () => ({
    cityName,
    data: {},
    osmMap: {},
    nowLocation: {},
    select: {
      city: '臺北市',
      area: '大安區',
    },
    results: null,
  }),
  methods: {
    async updateMarker() {
      // console.log('store', this.data);
      const storeInfo = this.data.filter((store) => {
        // console.log('store', store);
        if (!this.select.area) {
          return store.hsnNm === this.select.city;
        }
        return store.townNm === this.select.area;
      });

      storeInfo.forEach((store) => {
        let distance = 0;
        if ('coords' in this.nowLocation) {
          const targetLat = store.latitude;
          const targetLong = store.longitude;
          const nowLat = this.nowLocation.coords.latitude;
          const nowLong = this.nowLocation.coords.longitude;
          distance = this.getDistance(targetLat, nowLat, targetLong, nowLong);

          let valueDistance = distance;
          if (valueDistance > 999) {
            valueDistance = Math.round(distance / 1000);
            valueDistance = `約 ${valueDistance} 公里`;
          } else {
            valueDistance = `約 ${distance} 公尺`;
          }

          const index = this.data.indexOf(store);
          this.data[index].distance = valueDistance;
        }
        osm.addMapMarker(store.longitude, store.latitude, store, distance);
      });
      this.penTo(storeInfo[0]);
    },
    removeMarker() {
      osm.removeMapMarker();
    },
    penTo(store) {
      let distance = 0;
      if ('coords' in this.nowLocation) {
        const targetLat = store.latitude;
        const targetLong = store.longitude;
        const nowLat = this.nowLocation.coords.latitude;
        const nowLong = this.nowLocation.coords.longitude;
        distance = this.getDistance(targetLat, nowLat, targetLong, nowLong);
      }
      osm.penTo(store.longitude, store.latitude, store, distance);
    },
    updateSelect() {
      this.removeMarker();
      this.updateMarker();
      const pharmacy = this.data.find(item => item.townNm === this.select.area);

      let distance = 0;
      if ('coords' in this.nowLocation) {
        const targetLat = pharmacy.latitude;
        const targetLong = pharmacy.longitude;
        const nowLat = this.nowLocation.coords.latitude;
        const nowLong = this.nowLocation.coords.longitude;
        distance = this.getDistance(targetLat, nowLat, targetLong, nowLong);
      }

      osm.penTo(pharmacy.longitude, pharmacy.latitude, pharmacy, distance);
    },
    async getLocation() {
      // const url = `https://maps.googleapis.com/maps/api/geocode/json?address=${properties.address}+Parkway,+Mountain+View,+CA&key=${apiKey}`;
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(this.getTown);
      }
    },
    async getTown(position) {
      this.nowLocation = position;

      const lat = position.coords.latitude;
      const lng = position.coords.longitude;
      const res = await this.axios.get(`https://nominatim.openstreetmap.org/reverse?format=jsonv2&lat=${lat}&lon=${lng}`);
      const city = res.data.address.state;
      const area = res.data.address.suburb;
      this.select.city = city.replace('台', '臺');
      this.select.area = area.replace('台', '臺');

      this.updateSelect();
      // console.log(`The city is ${city}`, `The area is ${area}`);
      // const latlng = `${position.coords.latitude},${position.coords.longitude}`;
      // const apiKey = 'AIzaSyCyndMx2CpUy10uU5LJX6VHkLHwAhgdIS0';
      // this.axios.get(`https://maps.googleapis.com/maps/api/geocode/json?latlng=${latlng}&key=${apiKey}`).then((mapResponse) => {
      //   const city = mapResponse.data.results[0].address_components[3].long_name;
      //   const area = mapResponse.data.results[0].address_components[2].long_name;
      //   this.select.city = city.replace('台', '臺');
      //   this.select.area = area.replace('台', '臺');
      //   // console.log(`The city is ${city}`, `The area is ${area}`);

      //   this.updateSelect();
      // });
    },
    async getData() {
      const res = await this.axios.get('https://3000.gov.tw/hpgapi-openmap/api/getPostData');
      // console.log(res.data);
      this.data = res.data;
      this.getLocation();
      this.updateMarker();
    },
    getDistance(lat1, lat2, lon1, lon2) {
      const newLon1 = this.toRadian(lon1);
      const newLat1 = this.toRadian(lat1);
      const newLon2 = this.toRadian(lon2);
      const newLat2 = this.toRadian(lat2);

      const deltaLat = newLat2 - newLat1;
      const deltaLon = newLon2 - newLon1;

      const a = Math.sin(deltaLat / 2) ** 2;
      const b = Math.sin(deltaLon / 2) ** 2;
      const c = a + Math.cos(lat1) * Math.cos(lat2) * b;
      const d = 2 * Math.asin(Math.sqrt(c));
      const EARTH_RADIUS = 6371;
      return Math.round(d * EARTH_RADIUS * 1000);
    },
    toRadian(degree) {
      return degree * Math.PI / 180;
    },
  },
  async mounted() {
    // OSM
    osmMap = L.map('map', {
      center: [25.03, 121.55],
      zoom: 17,
      scrollWheelZoom: false,
      dragging: !L.Browser.mobile,
    });

    L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
      attribution: '<a target="_blank" href="https://www.openstreetmap.org/">© OpenStreetMap 貢獻者</a>',
      maxZoom: 18,
    }).addTo(osmMap);

    this.getData();
  },
};
</script>

